import './stepperKebub.css'
import {Step, Stepper} from 'react-form-stepper';

export const StepperKebab = ({activeStep}: { activeStep: number }): JSX.Element => {

    return (
        <div className={'stepperKebab'}>
            <Stepper connectorStateColors={true} activeStep={activeStep}>
                <Step label="Шаг 1"> </Step>
                <Step label="Шаг 2"> </Step>
                <Step label="Шаг 3"> </Step>
                <Step label="Шаг 4"> </Step>
                <Step label="Шаг 5"> </Step>
            </Stepper>
        </div>
    )
}