import styles from './BuyButton.module.scss';

type Props = {
  onClick: () => void;
}

export const BuyButton = (props : Props): JSX.Element => {
  return (
    <button
      type='button'
      className={styles.button}
      onClick={props.onClick}
    >
      <span>Купить</span>
    </button>
  )
}
