import {useForm} from "react-hook-form";
import {ErrorsMessage} from "../../../ui/errorsMessage/errorsMessage";
import {Input} from "../../../ui/inputs/input/input";
import stranger from "../../authorization/authorization.module.css";
import style from './../registration.module.css';
import {PropsForm} from "./registrationForms";
import {ButtonEnter} from "../../../ui/buttons/buttonEnter/buttonEnter";

export function RegistrationForm1<T>({onSubmitHandle, error, toggleActiveStep}: PropsForm<T>): JSX.Element {

    const {handleSubmit, control, formState: {errors}} = useForm<any>({
        defaultValues: {
            username: '',
            lastName: '',
            idPartner: '',
        },
        mode: 'onChange',
    });

    const onSubmit = (data: any) => {
        onSubmitHandle(data)
    }

    return (
        <>
            <h1>Регистрация</h1>
            <span>Как вас зовут и кто вас пригласил?</span>

            <form onSubmit={handleSubmit(onSubmit)}>
                <div className={`${stranger.formItem}`}>
                    {error ? <ErrorsMessage textMessage={''}/> :
                        <label htmlFor="username">Имя</label>}
                    <Input control={control} name='username' rules={{required: true}}/>
                    {errors.username &&
                    <ErrorsMessage textMessage={''}/>}
                </div>

                <div className={`${stranger.formItem}`}>
                    <label htmlFor="lastName">Фамилия</label>
                    <Input control={control} name='lastName' rules={{required: true}}/>
                    {errors.lastName &&
                    <ErrorsMessage textMessage={''}/>}
                </div>

                <div className={`${stranger.formItem}`}>
                    <label htmlFor="idPartner">ID Партнера</label>
                    <Input control={control} name='idPartner' rules={{required: true}}/>
                    {errors.idPartner &&
                    <ErrorsMessage textMessage={''}/>}
                </div>

                <div className={`${stranger.footerForm} ${style.footerForm}`}>
                    <ButtonEnter onClick={() => toggleActiveStep(1)} value={'Продолжить'}/>
                </div>
            </form>
        </>
    )
}