import styles from './NavBarOpen.module.scss';
import { ReactSVG } from 'react-svg';
import bigLogoIcon from '../icons/big-logo.svg';
import dashboardIcon from '../icons/dashboard.svg';
import contactsIcon from '../icons/contacts.svg';
import structureIcon from '../icons/structure.svg';
import calendarIcon from '../icons/calendar.svg';
import partnersIcon from '../icons/partners.svg';
import promoIcon from '../icons/promo.svg';
import historyOpenIcon from '../icons/history-hover.svg';

export const NavBarOpen = () => {
  return (
    <nav
    className={styles.nav}
    >
        <div className={styles.logo}>
        <ReactSVG className={styles.iconLogo} src={bigLogoIcon} />
      </div>
      <ul className={styles.list}>
        <li className={`${styles.item} ${styles.active}`}>
          <ReactSVG className={styles.dashboardIcon} src={dashboardIcon} />
          <span className={styles.text}>Дашборд</span>
        </li>
        <li className={styles.item}>
          <ReactSVG className={styles.contactsIcon} src={contactsIcon} />
          <span className={styles.text}>Контракты</span>
        </li>
        <li className={styles.item}>
          <ReactSVG className={styles.structureIcon} src={structureIcon} />
          <span className={styles.text}>Структура</span>
        </li>
        <li className={styles.item}>
          <ReactSVG className={styles.calendarIcon} src={calendarIcon} />
          <span className={styles.text}>Календарь</span>
        </li>
        <li className={styles.item}>
          <ReactSVG className={styles.partnersIcon} src={partnersIcon} />
          <span className={styles.text}>Партнеры</span>
        </li>
        <li className={styles.item}>
          <ReactSVG className={styles.promoIcon} src={promoIcon} />
          <span className={styles.text}>Промо</span>
        </li>
      </ul>
      <div className={styles.history}>
          <ReactSVG className={styles.historyIcon} src={historyOpenIcon} />
      </div>
    </nav>
  )
}
