import style from "./toggleTheme.module.css";
import logo from "../../assets/icons/mango_logo.svg";
import iconSwitch from "../../assets/icons/iconSwitch.svg";
import { ReactSVG } from "react-svg";

export const TopBlock = (): JSX.Element => {
    return (
        <div className={`${style.topBlockWrapper}`}>
            <div className={`${style.logo}`}>
                <ReactSVG src={logo} alt="logo"/>
            </div>
            <ToggleTheme/>
        </div>
    )
}

const ToggleTheme = (): JSX.Element => {

    return (
        <div className={`${style.toggleThemeWrapper}`}>
            <div className={`${style.currentThemeValue}`}>Темная тема</div>
            <div className={`${style.toggleThemeSwitch}`}>
                <ReactSVG src={iconSwitch} alt="iconSwitch"/>
            </div>
        </div>
    )
}