import {useState} from "react"
import { BuyButton } from '../../../ui/buttons/BuyButton/BuyButton';
import { TakeOutButton } from '../../../ui/buttons/TakeOutButton/TakeOutButton';

import styles from './RZMBalance.module.scss';

import { ReactSVG } from 'react-svg';
import RZMBalanceIcon from '../icons/RZMBalance.svg';
import scheduleIcon from '../icons/schedule.svg'
import numbersIcon from '../icons/RZMnumbers.svg'
import info from '../icons/information.svg'


export const RZMBalance = (): JSX.Element => {
  const [balance, /* setBalance */] = useState('27 348.01')
  const [income, /* setIncome */] = useState('124')

  return (
    <div className={styles.wrapper}>
      <div className={styles.RZMBalance}>
        <div className={styles.blockName}>
          <ReactSVG className={styles.RZMBalanceIcon} src={RZMBalanceIcon} />
          <span>Баланс RZM</span>
          <ReactSVG className={styles.info} src={info} />
        </div>
        <div className={styles.balance}>
          {balance}
        </div>
        <div className={styles.todayIncome}>
          <span>Сегодня: </span>{`+${income} RZM`}
        </div>
        <div className={styles.shedule}>
          <ReactSVG className={styles.schedule} src={scheduleIcon} />
        </div>
        <div>
          <ReactSVG className={styles.numbers} src={numbersIcon} />
        </div>
        <div className={styles.buttons}>
          <div className={styles.button}>
            <BuyButton onClick={() => null} />
          </div>
          <div className={styles.button}>
            <TakeOutButton onClick={() => null} />
          </div>
        </div>
      </div>
    </div>
  )
}
