import {ReactSVG} from "react-svg";
import {useForm} from "react-hook-form";
import {ErrorsMessage} from "../../../ui/errorsMessage/errorsMessage";
import {Input} from "../../../ui/inputs/input/input";
import stranger from "../../authorization/authorization.module.css";
import style from "../registration.module.css";
import iconRepeatSmsSend from "../../../assets/icons/iconRepeatSmsSend.svg";
import {FooterForm} from "./footerForm";
import { PropsForm } from "./registrationForms";

export function RegistrationForm3<T> ({onSubmitHandle, error, toggleActiveStep}: PropsForm<T>): JSX.Element {
    const phone = '46846'

    const {handleSubmit, control, formState: {errors}} = useForm<any>({
        defaultValues: {
            phone: ''
        },
        mode: 'onChange',
    });

    const onSubmit = (data: any) => {
        onSubmitHandle(data)
    }

    return (
        <>
            <h1>Подтверждение</h1>
            <span>Мы отправили вам сообщение на телефон
              <span className={'phone'}> {phone} </span>введите его ниже</span>


            <button className={`${style.repeatSmsSend}`}>
                <ReactSVG src={iconRepeatSmsSend}/>Отправить еще раз
            </button>

            <form onSubmit={handleSubmit(onSubmit)}>
                <div className={`${stranger.formItem}`}>
                    {error ? <ErrorsMessage textMessage={''}/> :
                        <div className={`${style.inputsPhoneWrapper}`}>
                            <Input control={control} name='phone1' rules={{required: true}}/>
                            <Input control={control} name='phone2' rules={{required: true}}/>
                            <Input control={control} name='phone3' rules={{required: true}}/>
                            <Input control={control} name='phone4' rules={{required: true}}/>
                        </div>}
                    {errors.phone &&
                    <ErrorsMessage textMessage={''}/>}
                </div>

                <FooterForm stepPrev={1} stepNext={3} toggleActiveStep={toggleActiveStep}>
                    Продолжить
                </FooterForm>
            </form>
        </>
    )
}