import styles from './NewsItem.module.scss';

import { ReactSVG } from 'react-svg';
import eyeIcon from '../../icons/newsEye.svg';
import calendarIcon from '../../icons/newsCalendar.svg';

type Props = {
  news: News;
}

export const NewsItem = (props: Props): JSX.Element => {
  const { image, title, text, views, date } = props.news;

  return (
    <div className={styles.wrapper}>
      <div className={styles.image}>
        <img src={image} alt="NewsImage" />
      </div>
      <div className={styles.text}>
        <h2 className={styles.title}>{title}</h2>
        <p className={styles.textNews}>{text}</p>
      </div>
      <div className={styles.info}>
        <ReactSVG className={styles.eyeIcon} src={eyeIcon} />
        <span>{views}</span>
        <ReactSVG className={styles.calendarIcon} src={calendarIcon} />
        <span>{date}</span>
      </div>
    </div>
  )
}
