import styles from './TotalContracts.module.scss';
import { ReactSVG } from 'react-svg';
import notebookIcon from '../../icons/notebook.svg';

type Props = {
  text: string;
  sum: string;
  icon: string;
}


export const TotalContracts: React.FC<Props> = ({ text, sum, icon }) => {
  return (
    <div className={styles.wrapper}>
      <div className={styles.TotalContracts}>
        <div className={styles.iconBox}>
          <ReactSVG className={styles.notebookIcon} src={notebookIcon} />
        </div>
        <div className={styles.sum}>
          {sum}
            <ReactSVG className={styles.currencyIcon} src={icon} />
        </div>
        <div className={styles.text}>
          <span> Общая сумма контрактов</span> {text}
        </div>
      </div>
    </div>
  )
}
