import { createAsyncThunk } from '@reduxjs/toolkit';
import { baseRequestAxios } from '../../api/baseRequest';
import {AuthorizationRequest, AuthorizationResponse, CreateClientResponse} from "../../api/type/authorisation";

export const authorizations = createAsyncThunk(
    'authorization',
    async (requestData: AuthorizationRequest, {rejectWithValue}) => {
        try {
            const response  = await baseRequestAxios.post<AuthorizationResponse>("/oauth/v2/token", {
                grant_type : requestData.grant_type,
                client_id: requestData.client_id,
                client_secret : requestData.client_secret,
                username : requestData.username,
                password: requestData.password,
            })
            if (response.status === 200) {
                return response.data
            }
        } catch (errors){
            return  rejectWithValue(errors)
        }
    }
)
export const createClient = createAsyncThunk(
    'createClient',
    async (_, {rejectWithValue}) => {
        try {
            const response  = await baseRequestAxios.get<CreateClientResponse>("/create-client")
            if (response.status === 200) {
                return response.data
            }
        } catch (errors){
            return  rejectWithValue(errors)
        }
    }
)
