import Slider from "react-slick";
import {ReactSVG} from "react-svg";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import './customSlider.css'
import screen1 from '../../../../assets/images/screen1.svg'
import screen2 from '../../../../assets/images/screen2.svg'
import screen3 from '../../../../assets/images/screen3.svg'
import screen4 from '../../../../assets/images/screen4.svg'
import screen5 from '../../../../assets/images/screen5.svg'
import screen6 from '../../../../assets/images/screen6.svg'

export const SliderLazyLoad = () => {

  const settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
    initialSlide: 2,
    autoplay: true,
  };

  return (
      <div className={'sliderWrapper'}>
        <Slider className={'customSlider'} {...settings}>
          <div className={'sliderItem'}>
            <ReactSVG src={screen1} />
            <h1>Счета с открытыми ссылками <br /> в свободном доступе</h1>
          </div>
          <div>
            <ReactSVG src={screen2} />
            <h1>Реальная процентная <br /> ставка</h1>
          </div>
          <div>
            <ReactSVG src={screen3} />
            <h1>Система бонусов <br /> и поощрений</h1>
          </div>
          <div>
            <ReactSVG src={screen4} />
            <h1>Политика активных <br /> инвестиций</h1>
          </div>
          <div>
            <ReactSVG src={screen5} />
            <h1>Большая команда <br /> единомышленников</h1>
          </div>
          <div>
            <ReactSVG src={screen6} />
            <h1>Образовательные тренинги <br /> и вебинары</h1>
          </div>
        </Slider>
      </div>
  );
}