import {useForm} from "react-hook-form";
import {ErrorsMessage} from "../../../ui/errorsMessage/errorsMessage";
import {InputCheckBox} from "../../../ui/inputs/inputCheckBox/inputCheckBox";
import {PropsForm} from "./registrationForms";
import style from "../registration.module.css";
import {FooterForm} from "./footerForm";

export function RegistrationForm5<T>({onSubmitHandle, error, toggleActiveStep}: PropsForm<T>): JSX.Element {

    const {handleSubmit, control, formState: {errors}} = useForm<any>({
        defaultValues: {
            policy1: false,
            policy2: false,
            policy3: false,
            policy4: false,
            policy5: true,
        },
        mode: 'onChange',
    });

    const onSubmit = (data: any) => {
        onSubmitHandle(data)
    }

    return (
        <>
            <h1>Соглашение</h1>
            <span>Прочитайте документы и подтвердите, что вы соглашаетесь со всеми условиями</span>

            <form onSubmit={handleSubmit(onSubmit)}>
                <div className={`${style.footerFormCheckBox}`}>
                    <InputCheckBox control={control} name='policy' rules={{required: true}}/>
                    {error ? <ErrorsMessage textMessage={''}/> :
                        <label htmlFor="policy">Я прочитал и согласен с политикой конфиденциальности</label>}
                    {errors.policy &&
                    <ErrorsMessage textMessage={''}/>}
                </div>

                <div className={`${style.footerFormCheckBox}`}>
                    <InputCheckBox control={control} name='policy2' rules={{required: true}}/>
                    {error ? <ErrorsMessage textMessage={''}/> :
                        <label htmlFor="policy2">Я прочитал и согласен с пользовательским соглашением</label>}
                    {errors.policy2 &&
                    <ErrorsMessage textMessage={''}/>}
                </div>

                <div className={`${style.footerFormCheckBox}`}>
                    <InputCheckBox control={control} name='policy3' rules={{required: true}}/>
                    {error ? <ErrorsMessage textMessage={''}/> :
                        <label htmlFor="policy3">Я прочитал и согласен с использованием файлов cookie</label>}
                    {errors.policy3 &&
                    <ErrorsMessage textMessage={''}/>}
                </div>

                <div className={`${style.footerFormCheckBox}`}>
                    <InputCheckBox control={control} name='policy4' rules={{required: true}}/>
                    {error ? <ErrorsMessage textMessage={''}/> :
                        <label htmlFor="policy4">Я прочитал и согласен с политикой риска</label>}
                    {errors.policy4 &&
                    <ErrorsMessage textMessage={''}/>}
                </div>

                <div className={`${style.footerFormCheckBox}`}>
                    <InputCheckBox control={control} name='policy5' rules={{required: true}}/>
                    {error ? <ErrorsMessage textMessage={''}/> :
                        <label htmlFor="policy5">Согласиться со всеми условиями</label>}
                    {errors.policy5 &&
                    <ErrorsMessage textMessage={''}/>}
                </div>
                <FooterForm toggleActiveStep={toggleActiveStep} stepPrev={3} stepNext={5}>
                    Завершить
                </FooterForm>

            </form>
        </>
    )
}