import styles from './TakeOutButton.module.scss';

type Props = {
  onClick: () => void;
}

export const TakeOutButton = (props : Props): JSX.Element => {
  return (
    <button
      type='button'
      className={styles.button}
      onClick={props.onClick}
    >
      <span>Вывести</span>
    </button>
  )
}
