import {NavLink} from "react-router-dom";
import {ReactSVG} from "react-svg";
import iconBtnEnter from "../../assets/icons/iconBtnEnter.svg";
import iconQuestions from "../../assets/icons/iconQuestions.svg";
import {TopBlock} from "../../ui/toggleTheme/topBlock";
import registrationBg from '../../assets/images/registration-bg.svg'
import iconPlayVideo from '../../assets/icons/iconPlayVideo.svg'
import style from "./registration.module.css";
import stranger from "../authorization/authorization.module.css";
import {RegistrationForms} from "./component/registrationForms";

export const Registration = (): JSX.Element => {

     const onSubmit = (data: any) => {
        console.log(data)
    }

    return (
        <div className={`${style.registration} ${stranger.authorization}`}>
            <div className={`${style.registrationForm} ${stranger.authorizationForm}`}>
                <TopBlock/>

                <div className={`${style.registrationFormWrapper}`}>
                    <RegistrationForms onSubmit={onSubmit}/>
                </div>

                <div className={`${stranger.authorizationFooter}`}>
                    <div className={`${stranger.authorizationFooter_rightBlock}`}>
                        <span>Уже есть аккаунт? </span>
                        <NavLink to={'/login'}>
                            <ReactSVG className={`${stranger.icon}`} src={iconBtnEnter}/>
                            Войти</NavLink>
                    </div>
                    <ReactSVG className={`${stranger.iconQuestions}`} src={iconQuestions}/>
                </div>

            </div>
            <div className={`${style.imagesBlock}`}>
                <ReactSVG src={registrationBg}/>
                <div className={`${style.description}`}>
                    <h1>Нужна помощь <br/> по регистрации?</h1>
                    <NavLink to={'#'}> <ReactSVG src={iconPlayVideo}/> Смотреть видео</NavLink>
                </div>
            </div>
        </div>
    )

}