import stranger from "../../authorization/authorization.module.css";
import {ErrorsMessage} from "../../../ui/errorsMessage/errorsMessage";
import {Input} from "../../../ui/inputs/input/input";
import {useForm} from "react-hook-form";
import {Select} from "../../../ui/select/select";
import {FooterForm} from "./footerForm";
import { PropsForm } from "./registrationForms";

export function RegistrationForm2<T> ({ onSubmitHandle, toggleActiveStep}: PropsForm<T>): JSX.Element {

    const {handleSubmit, control, formState: {errors}} = useForm<any>({
        defaultValues: {
            city: '',
            email: '',
            telephone: '',
        },
        mode: 'onChange',
    });

    const onSubmit = (data: any) => {
        onSubmitHandle(data)
    }

  return (
      <>
          <h1>Контактные данные</h1>
          <span>Где вы живете, ваш e-mail и телефон</span>

          <form onSubmit={handleSubmit(onSubmit)}>
              <div className={`${stranger.formItem}`}>
                  <Select  control={control} name='username' rules={{required: true}} />
                  {errors.sity &&
                  <ErrorsMessage textMessage={''}/>}
              </div>

              <div className={`${stranger.formItem}`}>
                  <div className={`${stranger.formItem_pass}`}>
                      <label htmlFor="E-mail">E-mail</label>
                      <Input control={control} name='Email' rules={{required: true}}/>
                      {errors.Email &&
                      <ErrorsMessage textMessage={''}/>}
                  </div>
              </div>

              <div className={`${stranger.formItem}`}>
                  <div className={`${stranger.formItem_pass}`}>
                      <label htmlFor="telephone">Телефон</label>
                      <Input control={control} name='telephone' rules={{required: true}}/>
                      {errors.telephone &&
                      <ErrorsMessage textMessage={''}/>}
                  </div>
              </div>

              <FooterForm stepPrev={0} stepNext={2} toggleActiveStep={toggleActiveStep}>
                  Продолжить
              </FooterForm>

          </form>
      </>
  )
}