import { FC } from "react";
import { ButtonEnter } from "../../../ui/buttons/buttonEnter/buttonEnter";
import stranger from "../../authorization/authorization.module.css";
import style from "../registration.module.css";

interface Props {
    stepPrev: number,
    stepNext: number,
    toggleActiveStep: (value: number) => void
}

export const FooterForm: FC<Props> = ({toggleActiveStep, stepPrev, stepNext, children}) => {
  return (
      <div className={`${stranger.footerForm} ${style.footerFormFlex}`}>
          <button className={style.backBtn} onClick={() => toggleActiveStep(stepPrev)}>Назад</button>
          <ButtonEnter onClick={() => toggleActiveStep(stepNext)}
                       value={children}/>
      </div>
  )
}