import {useForm} from "react-hook-form";
import {Link} from "react-router-dom";
import {Input} from "../../../ui/inputs/input/input";
import {InputPassword} from "../../../ui/inputs/inputPassword/inputPassword";
import iconQuestions from '../../../assets/icons/iconQuestions.svg'
import {AuthorizationData} from "../../../api/type/authorisation";
import {ErrorsMessage} from "../../../ui/errorsMessage/errorsMessage";
import iconBtnEnter from "../../../assets/icons/iconBtnEnter.svg";
import style from '../authorization.module.css'
import {ButtonEnter} from "../../../ui/buttons/buttonEnter/buttonEnter";

interface Props {
    onSubmitHandle: (data: AuthorizationData) => void,
    error: boolean,
}

export const AuthorizationForm = ({onSubmitHandle, error}: Props): JSX.Element => {

    const {handleSubmit, control, formState: {errors}} = useForm<AuthorizationData>({
        defaultValues: {
            username: '',
            password: '',
        },
        mode: 'onChange',
    });

    const onSubmit = (data: AuthorizationData) => {
        onSubmitHandle(data);
    }

    return (
        <form onSubmit={handleSubmit(onSubmit)}>
            <div className={`${style.formItem}`}>
                {error ? <ErrorsMessage textMessage={'Такого аккаунта не существует'}/> :
                    <label htmlFor="username">E-mail или телефон</label>}
                <Input<AuthorizationData> control={control} name='username' rules={{required: true}}/>
                {errors.username &&
                <ErrorsMessage textMessage={'Поле Телефон/Почта не заполнено'}/>}
            </div>

            <div className={`${style.formItem}`}>
                <label htmlFor="password">Пароль</label>
                <InputPassword<AuthorizationData> control={control} name='password' rules={{required: true}}/>
                {errors.password &&
                <ErrorsMessage textMessage={'Пароль введен некорректно'}/>}
            </div>
            <div className={`${style.footerForm}`}>
                <ButtonEnter value={'Войти'}>
                    <img src={iconBtnEnter} alt="iconBtnEnter"/>
                </ButtonEnter>
                <Link to='forgotPass' className={`${style.forgotPass}`}>
                    <img src={iconQuestions} alt="iconQuestions"/>Забыли пароль?</Link>
            </div>
        </form>
    )
}