import React, { useState } from 'react';
import { NavBarStandart } from './NavBarStandart/NavBarStandart';
import { NavBarOpen } from './navBarOpen/NavBarOpen';

import styles from './NavBar.module.scss';

export const NavBar: React.FC = () => {
  const [isHover, setIsHover] = useState(false);

  const addHoverProperty = () => {
    setIsHover(true);
  }

  const removeHoverProperty = () => {
    setIsHover(false);
  }

  return (
    <nav
      className={styles.nav}
      onMouseEnter={addHoverProperty}
      onMouseLeave={removeHoverProperty}
    >
      {isHover ? (<NavBarOpen />) : (<NavBarStandart />)}
    </nav>
  )
}
