import React, {useState} from "react"
import styles from './DNCBalance.module.scss';

import { ReactSVG } from 'react-svg';
import DNCBalanceIcon from '../icons/DNCBalance.svg';
import scheduleIcon from '../icons/schedule.svg'
import numbersIcon from '../icons/numbers.svg'

import { BuyButton } from '../../../ui/buttons/BuyButton/BuyButton';
import { TakeOutButton } from '../../../ui/buttons/TakeOutButton/TakeOutButton';

export const DNCBalance = (): JSX.Element => {
  const [balance, /* setBalance */] = useState('1 235 513.32')
  const [income, /* setIncome */] = useState('308')

  return (
    <div className={styles.wrapper}>
      <div className={styles.DNCBalance}>
        <div className={styles.blockName}>
          <ReactSVG
            className={styles.DNCBalanceIcon}
            src={DNCBalanceIcon}
          />
          <span>Баланс DNC</span>
        </div>
        <div className={styles.balance}>
          {balance}
        </div>
        <div className={styles.todayIncome}>
          <span>Сегодня: </span>{`+${income} DNC`}
        </div>
        <div className={styles.shedule}>
          <ReactSVG className={styles.scheduleIcon} src={scheduleIcon} />
        </div>
        <div>
          <ReactSVG className={styles.numbers} src={numbersIcon} />
        </div>
        <div className={styles.buttons}>
          <div className={styles.button}>
            <BuyButton onClick={() => null} />
          </div>
          <div className={styles.button}>
            <TakeOutButton onClick={() => null} />
          </div>
        </div>
      </div>
    </div>
  )
}
