import {useState} from "react";
import {useController, UseControllerProps} from "react-hook-form";
import passShow from '../../../assets/icons/passShow.svg';
import passHidden from '../../../assets/icons/passHidden.svg';
import passDotz from '../../../assets/icons/passDotz.svg';
import stranger from '../input/input.module.css';
import style from './inputPassword.module.css'

export function InputPassword<T>(props: UseControllerProps<T>): JSX.Element {
    const {field} = useController(props);
    const [showThis, toggleShowDoublePass] = useState(false);

    const toggleShowPassword = () => {
        toggleShowDoublePass(prev => !prev);
    };

    return (
        <div className={`${stranger.inputWrapper} ${style.inputPasswordWrapper}`}>
            <input autoComplete={"on"} data-sign={passDotz}
                   type={showThis ? 'text' : 'password'} {...field} />
            <img onClick={toggleShowPassword}
                 src={showThis ? passShow : passHidden} alt='showPass'/>
        </div>
    );
}