import {createSlice} from '@reduxjs/toolkit';
import {createClient, authorizations} from './authorizationThunk';

const authorizationSlice = createSlice({
    name: 'authorization',
    initialState: {
        authorization: {
            isAuth: false,
            error: false,
            refresh_token: '',
            access_token: '',
            expires_in: '',
            token_type: 'bearer',
            grant_type: 'password',
            scope: null,
        },
        createClient: {
            client_id: '',
            client_secret: '',
            error: false
        },
        loading: false
    },
    reducers: {
        signOut: (state) => {
            state.authorization.isAuth = false;
            localStorage.clear()
        },
    },
    extraReducers: (builder) => {
        //Authorization
        builder.addCase(authorizations.pending, (state) => {
            state.loading = true;
        });
        builder.addCase(authorizations.fulfilled, (state, action) => {
            if (action.payload) {
                state.authorization.isAuth = true
                state.authorization.access_token = action.payload.access_token
                state.authorization.scope = action.payload.scope
                state.authorization.expires_in = action.payload.expires_in
                state.authorization.token_type = action.payload.token_type

                localStorage.setItem('token', action.payload!.access_token);
            }
        });
        builder.addCase(authorizations.rejected, (state) => {
            state.loading = false;
            state.authorization.error = true;
        });

        //createClient
        builder.addCase(createClient.pending, (state) => {
            state.loading = true;
        });
        builder.addCase(createClient.fulfilled, (state, action) => {
            if (action.payload) {
                state.createClient.client_id = action.payload.client_id
                state.createClient.client_secret = action.payload.client_secret
            }
            state.loading = false
        });
        builder.addCase(createClient.rejected, (state) => {
            state.loading = false;
            state.createClient.error = true;
        });
    },

});

export const {signOut} = authorizationSlice.actions;
export const authorizationReducer = authorizationSlice.reducer;