import {RegistrationForm1} from "./registrationForm1";
import {RegistrationForm2} from "./registrationForm2";
import {RegistrationForm3} from "./registrationForm3";
import {RegistrationForm4} from "./registrationForm4";
import {RegistrationForm5} from "./registrationForm5";
import {StepperKebab} from "./stepperKebab/stepperKebab";
import {useState} from "react";


export interface PropsForm<T> {
    error: boolean,
    onSubmitHandle: (data: T) => void,
    toggleActiveStep: (value: number) => void
}
interface Props {
    onSubmit: (data: any) => void
}

export const RegistrationForms = ({onSubmit}: Props): JSX.Element => {
    const [activeStep, setActiveStep] = useState(0)
    const error = false

    const toggleActiveStep = (value: number) => {
        if (value === 5) {
            console.log(value)
        }
        setActiveStep(value)
    }

    return (
        <>
            <StepperKebab activeStep={activeStep}/>

            {activeStep === 0 && (<RegistrationForm1 toggleActiveStep={toggleActiveStep}
                                                     error={error} onSubmitHandle={onSubmit}/>)}

            {activeStep === 1 && (<RegistrationForm2 toggleActiveStep={toggleActiveStep}
                                                     error={error} onSubmitHandle={onSubmit}/>)}

            {activeStep === 2 && (<RegistrationForm3 toggleActiveStep={toggleActiveStep}
                                                     error={error} onSubmitHandle={onSubmit}/>)}

            {activeStep === 3 && (<RegistrationForm4 toggleActiveStep={toggleActiveStep}
                                                     error={error} onSubmitHandle={onSubmit}/>)}

            {activeStep === 4 && (<RegistrationForm5 toggleActiveStep={toggleActiveStep}
                                                     error={error} onSubmitHandle={onSubmit}/>)}
        </>
    )
}