import {ReactNode} from 'react';
import {Redirect, Route, Switch} from 'react-router-dom';
import {Authorization} from './authorization/authorization';
import {Registration} from './registration/registration';
import {Recovery} from "./recovery/recovery";
import {Dashboard} from "./dashboard/dashboard";
import {Contacts} from "./contacts/contacts";
import {Structure} from "./structure/structure";
import {History} from "./history/history";
import {Partners} from "./partners/partners";
import {Promo} from "./promo/promo";
import {News} from "./news/news";
import {Settings} from "./settings/settings";
import {PartnersOpen} from "./partnersOpen/partnersOpen";
import {NotFound} from "./notFound/notFound";
import {useSelector} from "react-redux";
import {AppStateType} from "../core/redux/store";

export interface RoutesType {
    name: string;
    path: string;
    component: ReactNode;
    exact?: boolean,
}


export const routesPublic: RoutesType[] = [
    {name: 'recovery', path: `/recovery`, exact: true, component: <Recovery/>},
    {name: 'login', path: `/login`, exact: true, component: <Authorization/>},
    {name: 'registration', path: `/registration`, exact: true, component: <Registration/>},
    {name: 'indexEntry', path: '/', exact: true, component: <Authorization/>},
];

export const routesPrivate: RoutesType[] = [
    {name: 'contacts', path: `/contacts`, exact: true, component: <Contacts/>},
    {name: 'structure', path: `/structure`, exact: true, component: <Structure/>},
    {name: 'history', path: `/history`, exact: true, component: <History/>},
    {name: 'partners', path: `/partners`, exact: true, component: <Partners/>},
    {name: 'promo', path: `/promo`, exact: true, component: <Promo/>},
    {name: 'news', path: `/news`, exact: true, component: <News/>},
    {name: 'settings', path: `/settings`, exact: true, component: <Settings/>},
    {name: 'partnersOpen', path: `/partnersOpen`, exact: true, component: <PartnersOpen/>},
    {name: 'notFound', path: `/notFound`, exact: true, component: <NotFound/>},
    {name: 'dashboard', exact: true, path: `/dashboard`, component: <Dashboard/>},
    {name: 'indexEntry', path: '/', component: <Authorization/>},
];

export const Routes = (): JSX.Element => {
    const {isAuth} = useSelector((state: AppStateType) => state.authorization.authorization)
    const token = localStorage.getItem('token');

    console.log(isAuth)
    console.log(token)

    return (
        <div className='app'>
            {token ?
                (<Switch>
                    {routesPrivate.map(item =>
                        <Route key={item.name} path={item.path} render={() => item.component}/>
                    )}
                    <Redirect to={'/dashboard'}/>
                </Switch>)
                :
                (<Switch>
                    {routesPublic.map(item =>
                        <Route key={item.name} path={item.path} render={() => item.component}/>
                    )}
                    {!isAuth && <Redirect to={'/login'}/>}
                </Switch>)}
        </div>
    );
};