import {useForm} from "react-hook-form";
import { ButtonEnter } from "../../../ui/buttons/buttonEnter/buttonEnter";
import {ErrorsMessage} from "../../../ui/errorsMessage/errorsMessage";
import {InputPassword} from "../../../ui/inputs/inputPassword/inputPassword";
import stranger from "../../authorization/authorization.module.css";
import style from "../registration.module.css";

interface Props {
    error: boolean,
    onSubmitHandle: (data: any) => void,
    toggleActiveStep: (value: number) => void
}

export const RegistrationForm4 = ({toggleActiveStep, error, onSubmitHandle}: Props): JSX.Element => {

    const {handleSubmit, control, formState: {errors}} = useForm<any>({
        defaultValues: {
            newPassword: '',
            repeatPassword: '',
        },
        mode: 'onChange',
    });

    const onSubmit = (data: any) => {
        onSubmitHandle(data)
    }

    return (
        <>
            <h1>Безопасность</h1>
            <span>Придумайте пароль и подтвердите его. Пароль должен содержать символы — Аа, 123, @#$</span>

            <form onSubmit={handleSubmit(onSubmit)}>
                <div className={`${stranger.formItem}`}>
                    {error ? <ErrorsMessage textMessage={''}/> :
                        <label htmlFor="repeatPassword">Новый пароль</label>}
                    <InputPassword control={control} name='repeatPassword' rules={{required: true}}/>
                    {errors.repeatPassword &&
                    <ErrorsMessage textMessage={''}/>}
                </div>

                <div className={`${stranger.footerForm} ${style.footerFormFlex}`}>
                    <button className={style.backBtn} onClick={() => toggleActiveStep(2)}>Назад</button>
                    <ButtonEnter onClick={() => toggleActiveStep(4)} value={'Продолжить'}/>
                </div>
            </form>
        </>
    )
}