import style from './buttonEnter.module.css'
import {FC, ReactNode} from "react";

interface Props {
    value: string | ReactNode,
    onClick?: () => void
}

export const ButtonEnter: FC<Props>  = ({value, children, onClick}): JSX.Element => {

    return (
        <button onClick={onClick} className={`${style.btn}`}>{children}{value}</button>
    )
}